body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-items a {
    text-decoration: none;
}

.app-body {
    text-align: center;
    overflow: auto;
}

.team-dashboard-container {
    padding-top: 20px;
    height: calc(100vh - 350px);
    min-height: 400px;
}

.upcoming-events-container {
    padding-top: 20px;
    height: 345px;
}

.team-member-profile-thumbnail {
    display: inline-block;
    margin-right: 30px;
}

.profile-image-circle-mini {
    display: inline-block;
    margin-right: 10px;
    padding: 0;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    overflow: hidden;
}

.add-profile-circle svg {
    font-size: 115px;
    margin-top: 18px;
    margin-left: -15px;
}

.profile-image-circle-mini img {
    width: 100%;
}

.modal-container {
    position: relative;
}
.modal-container .modal, .modal-container .modal-backdrop {
    position: absolute;
}

.events-container {
    width: 100%;
    height: 160px;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    padding-left: 20px;
    padding-right: 75px;
}
.calendar-card {
    display: inline-block;
    width: 400px;
    margin: 5px;
    height: 150px;
    margin-right: 20px;
}

.form-card-container {
    width: 100%;
    overflow-wrap: normal;
    overflow-x: hidden;
    overflow-y: scroll;
    white-space: pre-wrap;
    padding-top: 60px;
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.form-card {
    display: inline-block;
    width: 175px;
    height: 250px;
}